.btn {
  font-family: "Roboto";
  display: inline-block;
  padding: 14px 32px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  border-radius: 4px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  transition: background-color 0.5s;
  -webkit-transition: background-color 0.5s;
  -moz-transition: background-color 0.5s;
  -ms-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
}
.btn-outline-primary {
  color: var(--primary);
  background-color: var(--white);
  border: 1px solid var(--primary);
}
.btn-outline-primary:hover {
  color: var(--white);
  background-color: var(--primary);
}
.btn-primary {
  color: var(--white);
  background-color: var(--primary);
  border: 1px solid var(--primary);
}
.btn-primary:hover {
  color: var(--primary);
  background-color: var(--white);
}
.btn-github {
  color: var(--white);
  background-color: var(--dark-shade);
  border: var(--dark-shade);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.95);
  gap: 16px;
}
.btn-github:hover {
  color: var(--dark-shade);
  background-color: var(--white);
}
/* Global CSS Ends */
/* Main Headings */

/* Heading 1 */
h1 {
  font-size: 56px;
  font-weight: 700;
  line-height: 67px;
}
/* Heading 2 */
h2 {
  font-size: 48px;
  font-weight: 700;
  line-height: 58px;
}
/* Heading 3 */
h3 {
  font-size: 24px;
  font-weight: 700;
  line-height: 34px;
}

/* Pragraphs text large medium small */
/* Body 1 */
.text-lg {
  color: var(--dark-shade);
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 27px;
}
/* Body 2 */
.text-md {
  color: var(--dark-shade);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  overflow: hidden; /* Hide overflowing text */
  text-overflow: ellipsis; /* Add ellipsis for overflowing text */
  display: -webkit-box;
  -webkit-line-clamp: 3; /* Limit to 3 lines */
  -webkit-box-orient: vertical;
}
/* Body 3 */
.text-sm {
  color: var(--black);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 21px;
}

/* Section Titles */
/* Skills & About Me */
.section-title {
  color: var(--heading-color);
  font-size: 21px;
  text-align: center;
  font-style: normal;
  font-weight: 600;
  line-height: 32px;
  display: flex;
  align-items: flex-start;
}
/* Portfolio, MySkillsScore & Contact Me */
.sub-title {
  color: var(--heading-color);
  font-size: 16px;
  font-weight: 600;
  line-height: 24px;
}
/* Section Titles Ends */

/* Navbar Style Start */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 85.333px;
  background: var(--white);
  box-shadow: 0px 5.333px 80px 0 rgba(0, 0, 0, 0.1);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99999;
}

.navbar_icon_content {
  display: flex;
}

.navbar-items > ul {
  list-style: none;
  display: flex;
  align-items: flex-start;
  gap: 42.667px;
  text-decoration: none;
}
.navbar-items ul > li > a {
  text-decoration: none;
}
/* Navbar Content */
.navbar-content {
  color: var(--dark-shade);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  cursor: pointer;
}
.navbar-active-content {
  color: var(--primary);
}
.navbar-img {
  height: auto;
  width: 8vw;
  max-width: 100px;
  min-width: 60px;
}

.navbar-logo {
  display: flex;
  flex-direction: column;
  align-items: center;

}

.dark-mode-switch{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100px;
}
.navbar-items {
  display: contents;
}

/* Navbar Styler Ends */
/* Hero Section Style */
.hero-section {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 133.333px 85.333px 0 85.333px;
  align-items: center;
  justify-content: space-between;
  gap: 32px;
  background-color: var(--bg-shade);
}
.hero-section-content-box {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
}
.hero-section-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 21.333px;
}
.hero--section-content--box > button {
  margin-top: 21.333px;
}
.hero-section-title {
  color: var(--heading-color);
  font-size: 74.667px;
  font-weight: 700;
  line-height: 90px;
  align-self: stretch;
}
.hero-section-title-color {
  color: var(--primary);
}
.hero-section-description {
  color: var(--dark-shade);
  font-size: 20px;
  font-weight: 400;
  line-height: 36px;
}
.hero-section-img {
  display: flex;
  height: 754px;
  width: auto;
}
.hero-section-img > img {
  width: 100%;
  height: 100%;
}

.hero-section-image {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero-section-image img {
  width: 75%;
  margin-top: 25px;
}
/* Hero Section Style Ends */

/* Skills Section Style */
.skills-section {
  display: flex;
  padding: 149.33px 85.33px;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 40px;
}
.skill-score-section {
  display: flex;
  padding: 149.33px 85.33px;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 40px;
  background-color: var(--bg-shade);
}
.skills-section-heading {
  color: var(--heading-color);
  font-size: 54px;
  font-weight: 700;
  line-height: 77px;
}
.skills-section-container {
  display: grid;
  justify-content: center;
  align-items: flex-start;
  gap: 42.6px;
  grid-template-columns: repeat(3, 1fr);
}
.skills-section-card {
  display: flex;
  padding: 32px;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  flex: 1 0 0;
  border-radius: 10.6px;
  background: var(--bg-shade);
  min-height: 250px;
  border-bottom: 4px solid var(--bg-shade);
}
.skills-section-card:hover {
  border-bottom: 4px solid var(--primary);
}
.skills-section-card:hover .skills-section-description {
  color: var(--dark-shade);
}
.skills-section-img {
  display: flex;
  padding: 13.3px;
  justify-content: center;
  align-items: center;
  gap: 13.3px;
  border-radius: 10.6px;
  background: #fff;
  box-shadow: 0 0 16px 0 rgba(0, 0, 0, 0.1);
}
.skills-section-card-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  align-self: stretch;
}
.skills-section-title {
  color: var(--heading-color);
  font-size: 32px;
  font-weight: 700;
  line-height: 45px;
}
.skills-section-description {
  color: var(--black);
  font-size: 21.3px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
}

/*skills-score*/
.skills-score-section {
  display: flex;
  padding: 32px;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  flex: 1 0 0;
  border-radius: 10.6px;
  background: var(--bg-shade);
  min-height: 250px;
  border-bottom: 4px solid var(--bg-shade);
}

.skills-score-section-container {
  display: grid;
  justify-content: center;
  align-items: center;
  gap: 42.6px;
  grid-template-columns: repeat(4, 1fr);
  width: 100%;
  text-align: center;
}

.skills-score-section-title {
  color: var(--heading-color);
  font-size: 26px;
  font-weight: 700;
  line-height: 45px;
  padding-bottom: 10px;
}

.skills-score-card {
  position: relative;
}

.skills-score-card:hover .circular-progressbar-wrapper {
  filter: blur(5px);
}

.circular-progressbar-img {
  width: auto;
  height: 70px;
}

.circular-progressbar {
  position: relative;
  z-index: 2;
}

.circular-progressbar-wrapper {
  position: relative;
  z-index: 1;
}

.score-display {
  display: none; /* Standaard wordt het niet weergegeven */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 2rem;
  color: white;
  z-index: 3;
  background-color: var(--heading-color);
  padding: 5px 10px;
  border-radius: 5px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
  margin-top: 25px;
}

.skills-score-card:hover .score-display {
  display: block; /* Wanneer erover wordt bewogen, wordt het weergegeven */
}
/* Skills Style Ends */

/* About Us */
.about-section {
  display: grid;
  padding: 0px 85.3px 133.3px 85.3px;
  align-items: center;
  gap: 114.6px;
  grid-template-columns: repeat(2, 1fr);
}
.about-section-img > img {
  width: 100%;
  height: 100%;
}
/* About Us Ends */

/* My Portfolio Starts */
.portfolio-section {
  display: flex;
  padding: 0px 85px 112px 85px;
  flex-direction: column;
  align-items: flex-start;
  gap: 80px;
}
.portfolio-container-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.portfolio-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 21.3px;
}
.portfolio-section-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 42.6px;
  width: 100%;
}
.portfolio-section-img {
  border-radius: 8px;
  width: 100%;
  height: 250px; /* Fixed height for image container */
  overflow: hidden;
}

.portfolio-section-img > img {
  width: 100%;
}

.portfolio-section-img img{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.portfolio-section-card {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 32px;
  background: #fff;
  box-shadow: 0 0 32px 0 rgba(0, 0, 0, 0.15);
  cursor: pointer;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  height: 600px;
}
.portfolio-section-card-content {
  display: flex;
  padding: 32px;
  flex-direction: column;
  align-items: flex-start;
  gap: 16px;
  align-self: stretch;
  flex: 1;
}
.portfolio-section-title {
  color: var(--heading-color);
}
.portfolio-link {
  text-decoration: none;
  display: flex;
  gap: 16px;
  border-bottom: 1.33px solid var(--primary);
  padding-bottom: 10.6px;
  font-weight: 600;
}
.portfolio-section-card:hover path {
  stroke: #006b6a;
}
.portfolio-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  padding: 10px 16px;
  border-radius: 6px;
  transition: all 0.3s ease;
  width: fit-content;
  margin-top: 8px;
}

.portfolio-button svg {
  transition: transform 0.3s ease;
}

.portfolio-button:hover svg {
  transform: translate(3px, -3px);
}
/* Portfolio Ends */

/* MySkillsScore */
.testimonial-section {
  display: flex;
  padding: 112px 84.3px;
  flex-direction: column;
  align-items: flex-start;
  gap: 80px;
  background-color: var(--bg-shade);
}
.sections-heading {
  color: var(--heading-color);
}
.testimonial-section-card {
  display: flex;
  padding: 42.6px;
  flex-direction: column;
  align-items: flex-start;
  gap: 42.6px;
  flex: 1 0 0;
  border-radius: 8px;
  border: 1.33px solid #006b6a;
}
.testimonial-section-card-reviews {
  display: flex;
  align-items: flex-start;
  gap: 5.33px;
}
.testimonial-section-card-author-detail {
  display: flex;
  align-items: center;
  gap: 21.3px;
}
.testimonial-author-name {
  font-weight: 600;
  color: var(--heading-color);
}
.testimonial-author-designation {
  color: var(--dark-shade);
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
}
/* MySkillsScore Ends */
.contact-section {
  display: flex;
  padding: 0px 85px 30px 85px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  gap: 64px;
}
.contact-form-container {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: repeat(5, auto);
  width: 40%;
  row-gap: 32px;
}
.container {
  display: grid;
  grid-template-columns: repeat(2, auto);
  column-gap: 32px;
  row-gap: 32px;
}
.contact-label {
